
.about {
    padding: 2vh 10vh 2vh 10vh;
    background-color:var(--background-main-color);
  }
  
  .about span {
    color: var(--main-font-color);
  }
  
  .divider {
    display: flex;
    align-items: center;
  }
  
  .divider span {
    font-family: var(--font-subheading);
    font-weight: bold;
    font-size: 8vh;
    flex-shrink: 0;
    padding-right: 10px; /* Space between text and line */
  }

  .marker {
    display: flex;
    align-items: center; /* Center align items vertically */
    margin-bottom: 20px;
  }
  
  .y-mark {
    width: 50%;
    display: flex; /* Use flex to position text and line */
    align-items: center; /* Center align items vertically */
  }
  
  .y-mark span {
    font-size: 5vh;
    flex-shrink: 0; /* Space between text and line */
  }
  
  .y-mark::before {
    content: "";
    flex-grow: 1;
    height: 2px; /* Line thickness */
    background-color: var(--main-font-color);
  }

  .marker .par span {
    color: var(--main-font-color);
    font-weight: bold;
  }
  
  .about p {
    padding-left: 2vh;
    text-align: left;
    color: var(--submain-font-color);
  }

  .about-content {
    display: flex;
    flex-direction: row;
    font-size: 4vh;
    color: var(--submain-font-color);
  }
  
  .attributes{
    width: 50%;
    text-align: right;
    padding-right: 1vh;
  
  }
  .attributes-cor{
    width: 50%;
    text-align: left;
    padding-left: 1vh;
  }
  